body {
  background-color: #faebd7;
  padding: 50px;
  font-family: Arial, sans-serif;
  position: relative;
}

.loader {
  width: 60px;
  height: 60px;
  border: 16px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  margin: 50px auto;
  animation: spin 2s linear infinite;
  position: absolute;
  left: 0;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.breed-select {
  width: auto;
  background-color: rgba(151, 126, 126, .4);
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 20px;
  margin-right: 250px;
  padding: 30px;
  font-size: 16px;
}

.ss-search, .ss-values {
  width: min(80% - 1em, 69em);
  background-color: rgba(151, 126, 126, .4);
}

.cat-info {
  width: min(100% - 1em, 69em);
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  display: none;
}

.cat-info h2 {
  filter: drop-shadow(12px 12px 7px rgba(106, 43, 43, .4));
  margin-bottom: 10px;
  font-size: 24px;
}

.cat-info p {
  filter: drop-shadow(12px 12px 7px rgba(0, 0, 0, .4));
  margin-bottom: 10px;
  font-size: 16px;
}

.cat-info img {
  width: min(80% - 1em, 69em);
  height: auto;
  margin-left: auto;
  margin-right: auto;
  filter: drop-shadow(12px 12px 7px rgba(0, 0, 0, .7));
  border-radius: 2%;
  margin-top: 50px;
  display: block;
}

